<template>
  <div id="home">
    <navbar id="navbar"><label slot="center">购物街</label></navbar>
    <tabcontrol :text="['流行','新款','精选']"
                @tabclick="tabclick"
                ref="tabcontrol1" class="tabcontrol" v-show="istcshow"></tabcontrol>
    <scroll
      class="content" ref="scroll"
      :pullingup="true"
      :probeType="3"
      @scroll="contentscroll"
      @pullingup="pullingup"
    >
      <homeswiper :banners="banners" @swiperload="swiperload"></homeswiper>
      <recommend :recommends="recommends"></recommend>
      <feature-view></feature-view>
      <tabcontrol :text="['流行','新款','精选']"
        @tabclick="tabclick"
        ref="tabcontrol"></tabcontrol>
      <goods :goods="showtype"></goods>
    </scroll >
    <backtop @click.native="top" v-show="isshow"></backtop>
  </div>
</template>

<script>
//公共组件
import navbar from "@/components/common/navbar/navbar";
import tabcontrol from "@/components/common/tabControl/tabcontrol";
import scroll from "@/components/common/scroll/scroll";
//业务相关
import goods from "@/components/content/goos/goods";
//子组件
import homeswiper from "@/views/home/homechild/homeswiper";
import recommend from "@/views/home/homechild/recommend";
import featureView from "@/views/home/homechild/featureView";
import backtop from "@/components/content/backtop/backtop";
//网络请求
import {getHomeMulidata,getHomeData} from "@/network/home";
//mixin
import {imgloag} from "@/components/content/mixin/mixin";


export default {
  name: "home",
  components:{
    navbar,
    homeswiper,
    recommend,
    featureView,
    tabcontrol,
    goods,
    scroll,
    backtop
  },
  data(){
    return{
      //轮播图
      banners:[],
      //推荐
      recommends:[],
      //流行，新款，精选
      goods:{
        'pop':{page:0,list:[]},
        'new':{page:0,list:[]},
        'sell':{page:0,list:[]}
      },
      goodstype:'pop',
      isshow:false,
      istcshow:false,
      tabsetTop:0,
      scrollY:0,

    }
  },
  //组件创建的时候就请求数据
  created() {
    this.getHomeMulidata()
    this.getHomeData('pop')
    this.getHomeData('new')
    this.getHomeData('sell')
  },
  mixins:[imgloag],
  activated() {
    this.$refs.scroll.scrollTo(0,this.scrollY,0)
    this.$refs.scroll.refresh()
  },
  deactivated() {
    this.scrollY=this.$refs.scroll.scroll.y
    this.$bus.$off('imagelode',this.allimgLoad)
  },
  computed:{
    showtype(){
      return this.goods[this.goodstype].list
    }
  },
  methods:{
    tabclick(index){
      switch (index){
        case 0:
          this.goodstype='pop'
          break
        case 1:
          this.goodstype='new'
          break
        case 2:
          this.goodstype='sell'
          break
      }
      this.$refs.tabcontrol1.con=index
      this.$refs.tabcontrol.con=index
    },
    //请求轮播图和推荐
    getHomeMulidata(){
      getHomeMulidata().then(res=>{
        this.banners=res.data.data.banner.list
        this.recommends=res.data.data.recommend.list
      })
    },
    //请求流行，推荐，精选数据
    getHomeData(type){
      const page=this.goods[type].page+1
      getHomeData(type,page).then(res=>{
        this.goods[type].list.push(...res.data.data.list)
        this.goods[type].page=1
        this.$refs.scroll.finishpullup()
      })
    },
    top(){
      this.$refs.scroll.scrollTo(0,0,500)
    },
    contentscroll(position){
      this.isshow=-position.y>1000
      this.istcshow=(-position.y)>this.tabsetTop
    },
    pullingup(){
      this.getHomeData(this.goodstype)
    },
    swiperload(){
      this.tabsetTop=this.$refs.tabcontrol.$el.offsetTop
    }
  }
}
</script>

<style scoped>
#home{
  height: 100vh;
  position: relative;
}
#navbar{
  background-color: var(--color-tint);
  color: white;
