  left: 0;
  right: 0;
  top: 0;
  z-index: 9;*/
}
.tabcontrol{
  position: relative;
  z-index: 9;
}
.content{
  position: absolute;
  left: 0;
  right: 0;
  top: 44px;
  bottom: 49px;
  overflow: hidden;
}
