<template>
  <swiper>
    <swiper-item v-for="item in banners">
      <a :href="item.link">
        <img :src="item.image" @load="swiperload">
      </a>
    </swiper-item>
  </swiper>
</template>

<script>
import Swiper from "@/components/common/swiper/Swiper";
import SwiperItem from "@/components/common/swiper/SwiperItem";

export default {
  name: "homeswiper",
  props:{
    banners:{
      type:Array,
      default(){
        return []
      }
    }
  },
  components:{
    Swiper, SwiperItem
  },
  data(){
    return {
      isemit:true
    }
  },
  methods:{
    swiperload(){
      if(this.isemit){
        this.$emit('swiperload')
        this.isemit=false
      }
    }

  }
}
</script>

